/* css for stars */

.color-rotation {
  /* recommend using rgb(0, 255, 200) as base color/stroke/fill */
  animation-name: hue-rotate-cycle;
  animation-duration: 320s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

@keyframes hue-rotate-cycle {
  0% {
    filter: hue-rotate(0deg)
  }

  50% {
    filter: hue-rotate(360deg)
  }

  100% {
    filter: hue-rotate(0deg)
  }
}

.star-color-cycle {
  /* recommend using rgb(0, 255, 200) as base color/stroke/fill */
  animation-name: star-hue-cycle;
  animation-duration: 6s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}


/* green - 315 - 320 */
/* blue - 5 - 10 */
/* purple - 105 - 110 */
/* gold - 260 - 265 */


@keyframes star-hue-cycle {
  0% {
    filter: hue-rotate(260deg)
  }

  25% {
    filter: hue-rotate(265deg)
  }

  26% {
    filter: hue-rotate(5deg)
  }

  50% {
    filter: hue-rotate(10deg)
  }

  51% {
    filter: hue-rotate(105deg)
  }

  75% {
    filter: hue-rotate(110deg)
  }

  76% {
    filter: hue-rotate(315deg)
  }

  100% {
    filter: hue-rotate(320deg)
  }
}

.star-gold {
  animation-name: star-gold-cycle;
  animation-duration: 6s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

@keyframes star-gold-cycle {
  0% {
    filter: hue-rotate(260deg)
  }

  100% {
    filter: hue-rotate(265deg)
  }
}

.star-purple {
  animation-name: star-purple-cycle;
  animation-duration: 6s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

@keyframes star-purple-cycle {
  0% {
    filter: hue-rotate(105deg)
  }

  100% {
    filter: hue-rotate(110deg)
  }
}

.star-green {
  animation-name: star-green-cycle;
  animation-duration: 6s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

@keyframes star-green-cycle {
  0% {
    filter: hue-rotate(315deg)
  }

  100% {
    filter: hue-rotate(320deg)
  }
}

.star-blue {
  animation-name: star-blue-cycle;
  animation-duration: 6s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

@keyframes star-blue-cycle {
  0% {
    filter: hue-rotate(5deg)
  }

  100% {
    filter: hue-rotate(10deg)
  }
}

.star-bkgnd-anim-hue {
  animation-name: star-bkgnd-hue-cycle;
  animation-duration: 48s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

@keyframes star-bkgnd-hue-cycle {
  0% {
    filter: hue-rotate(0deg)
  }

  100% {
    filter: hue-rotate(360deg)
  }
}

.star-bkgnd-anim-contrast {
  animation-name: star-bkgnd-contrast-cycle;
  animation-duration: 40s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

@keyframes star-bkgnd-contrast-cycle {
  0% {
    filter: contrast(400%)
  }

  40% {
    filter: contrast(200%)
  }

  40.1% {
    filter: contrast(100%)
  }

  45% {
    filter: contrast(200%)
  }

  100% {
    filter: contrast(400%)
  }

}


.sun-pos-anim {
  animation-name: sun-pos-cycle;
  animation-duration: 50s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

@keyframes sun-pos-cycle {
  0% {
    top: -18.5rem;
    left: -18rem;
  }

  40% {
    top: -18.5rem;
    left: -10rem;
  }

  50% {
    top: -8rem;
    left: -18rem;
  }

  90% {
    top: -8rem;
    left: -10rem;
  }

  100% {
    top: -18.5rem;
    left: -18rem;

  }
}

.sun-hue-anim {
  animation-name: hue-rotate-cycle;
  animation-duration: 120s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}